import React from 'react';

import Title_unit from '../props/Title_unit';
import Title_article from '../props/Title_article';
import List_article from '../props/List_article';

const Html_elemtnt = () => {

    const styles = {
        main: { width:'100%', height:'calc(100vh - 102px)', display:'flex', justifyContent:'center', padding:'20px',
            background:'linear-gradient(180deg,#0f2027,#203a43,#2c5364)' },

        section: { width:'100%', maxWidth:'1300px', height:'100%', overflowY:'auto', padding:'24px',
            background:'rgba(255,255,255,0.05)', borderRadius:'12px',
            boxShadow:'0 10px 30px rgba(0,0,0,.3)', color:'var(--cb-white)' },

        p: { fontSize:'16px', lineHeight:'1.8', margin:'12px 0 24px', fontFamily:'khmer', opacity:'.95' },

        list: { marginLeft:'20px', marginBottom:'24px', fontFamily: 'khmer' }
    };

    return (
        <main-center style={styles.main}>
            <section style={styles.section}>

                

            </section>
        </main-center>
    );
};

export default Html_elemtnt;
